/*
BEM = block, element, modifier
examples:
  myBlock
  myBlock__myElement
  myBlock__anotherElement
  myBlock__anotherElement--large
  myBlock__anotherElement--green
*/
/* SASS variables */
@font-face {
  font-family: LuloCleanOneBold;
  src: url("./font/Lulo_Clean_One_Bold.otf"); }

@font-face {
  font-family: LuloCleanOne;
  src: url("./font/Lulo_Clean_One.otf"); }

.InputWithButton {
  display: -ms-flexbox;
  display: flex;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  padding: 10px 10px; }

.InputWithButton__input {
  -ms-flex: 1 1;
      flex: 1 1;
  padding: 10px 5px 10px 0;
  font-family: LuloCleanOne, sans-serif;
  font-size: 16px;
  color: #000000;
  border: 0;
  outline: none; }

.InputWithButton__button {
  display: block;
  padding: 8px; }



