/*
BEM = block, element, modifier
examples:
  myBlock
  myBlock__myElement
  myBlock__anotherElement
  myBlock__anotherElement--large
  myBlock__anotherElement--green
*/
/* SASS variables */
@font-face {
  font-family: LuloCleanOneBold;
  src: url("./font/Lulo_Clean_One_Bold.otf"); }

@font-face {
  font-family: LuloCleanOne;
  src: url("./font/Lulo_Clean_One.otf"); }

.TextareaWithButton {
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  padding: 0;
  border: 0; }

.TextareaWithButton__textarea {
  display: block;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  height: 180px;
  resize: none;
  padding: 10px;
  font-family: LuloCleanOne, sans-serif;
  font-size: 16px;
  color: #000000; }

.TextareaWithButton__button {
  position: relative;
  display: block;
  /* this is a hack; would prefer a solution that vertical-centers the button on the input */
  top: -54px;
  right: 14px;
  margin-left: auto;
  padding: 8px; }



