/*
BEM = block, element, modifier
examples:
  myBlock
  myBlock__myElement
  myBlock__anotherElement
  myBlock__anotherElement--large
  myBlock__anotherElement--green
*/
/* SASS variables */
@font-face {
  font-family: LuloCleanOneBold;
  src: url("./font/Lulo_Clean_One_Bold.otf"); }

@font-face {
  font-family: LuloCleanOne;
  src: url("./font/Lulo_Clean_One.otf"); }

.Page {
  display: block;
  text-align: center;
  padding: 26px;
  max-width: 639px;
  margin: auto; }

.Page__babasLogo {
  width: 100%;
  max-width: 639px; }

.Page__header1 {
  font-family: LuloCleanOneBold, sans-serif; }

.Page__header1--blue {
  color: #22BBB4; }

.Page__header1--light {
  color: #FFFFFF; }

.Page__header1--orange {
  color: #FBAF5F; }

.Page__normalText {
  color: #676867;
  text-align: left;
  font-family: LuloCleanOne, sans-serif;
  font-size: 12px; }

.Page__normalText--light {
  color: #FFFFFF; }

.Page__videoWrapper {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%; }

.Page__video {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%; }

.Page__nextButton {
  position: fixed;
  display: block;
  width: calc(100% - (2 * 26px));
  max-width: 639px;
  margin: auto;
  bottom: 26px; }

.Page__buttonOverStatusBar {
  position: fixed;
  display: block;
  width: calc(100% - (2 * 26px));
  max-width: 639px;
  margin: auto;
  bottom: 80px; }

.Page__simpleQuestionShortAnswer {
  display: block; }

.Page__simpleQuestionHeader {
  font-family: LuloCleanOneBold, sans-serif;
  font-size: 14pt;
  color: #22BBB4;
  padding-bottom: 10px;
  text-align: center; }

.Page__simpleQuestionHeader--upper {
  padding-top: 0px; }

.Page__simpleQuestionHeader--mid {
  padding-top: 110px; }

.Page__simpleQuestionLongAnswer {
  display: block; }

.Page__dreamConfirmed {
  text-align: center; }

.Page__babasLogoSmall {
  width: 35%;
  max-width: 200px; }

.Page__iconCheckmark {
  width: 35%;
  max-width: 200px; }

.Page__fullBlueBackground {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #22BBB4;
  z-index: -1; }

.Page__statusBar {
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  background: #BFBFBF;
  padding: 10px; }

.Page__statusBar__progressbar {
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%); }

.Page__statusBar__buttons {
  float: right;
  margin-right: 10px; }

.Page__statusBar__buttons__button {
  border-radius: 10px;
  font-family: LuloCleanOne, sans-serif;
  color: #FFFFFF;
  font-size: 25px;
  background: #676867;
  padding: 5px 15px;
  text-decoration: none;
  border: none;
  margin-right: 3px; }

.Page__statusBar__buttons__button:hover {
  text-decoration: none; }

.Page__statusBar__buttons__button:focus {
  outline: none; }

.Page__statusBar__buttons__button:disabled,
.Page__statusBar__buttons__button:active {
  background: darkgray;
  outline: none;
  -webkit-box-shadow: inset 0px 0px 4px #ccc;
  box-shadow: inset 0px 0px 4px #ccc; }



