/*
BEM = block, element, modifier
examples:
  myBlock
  myBlock__myElement
  myBlock__anotherElement
  myBlock__anotherElement--large
  myBlock__anotherElement--green
*/
/* SASS variables */
@font-face {
  font-family: LuloCleanOneBold;
  src: url("./font/Lulo_Clean_One_Bold.otf"); }

@font-face {
  font-family: LuloCleanOne;
  src: url("./font/Lulo_Clean_One.otf"); }

.App {
  display: block; }

.AppTheme__element--blueBorder {
  border: #22BBB4 2px solid;
  border-radius: 0; }

.AppTheme__element--blueBorder:focus {
  outline: none; }

.AppTheme__largeButton {
  border-radius: 5px;
  font-family: LuloCleanOneBold, sans-serif;
  color: #FFFFFF;
  padding: 10px 20px 10px 20px;
  text-decoration: none;
  border: none; }

.AppTheme__largeButton--blue {
  background: #22BBB4;
  font-size: 20px; }

.AppTheme__largeButton--orange {
  background: #FBAF5F;
  font-size: 14px; }

.AppTheme__largeButton:hover {
  text-decoration: none; }

.AppTheme__largeButton:focus {
  outline: none; }

.AppTheme__largeButton:disabled,
.AppTheme__largeButton:active {
  background: #e0e0e0;
  outline: none;
  -webkit-box-shadow: inset 0px 0px 4px #ccc;
  box-shadow: inset 0px 0px 4px #ccc; }

.AppTheme__button--smallOrange {
  background: #FBAF5F;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border: 0;
  border-radius: 10px;
  font-family: LuloCleanOneBold, sans-serif;
  font-size: 18px;
  color: #FFFFFF;
  text-decoration: none; }

.AppTheme__button--smallOrange:hover {
  text-decoration: none; }

.AppTheme__button--smallOrange:focus {
  outline: none; }

.AppTheme__button--smallOrange:active {
  background: #e0e0e0;
  outline: none;
  -webkit-box-shadow: inset 0px 0px 4px #ccc;
  box-shadow: inset 0px 0px 4px #ccc; }

.App__modalMessage {
  font-family: LuloCleanOneBold, sans-serif;
  font-size: 16px;
  margin-bottom: 20px; }



